<template>
  <div class="grid">
    <div class="col-12 lg:col-12 xl:col-12">
      <div class="card mb-0">
        <div class="mb-4">
          <h4>Prodi</h4>
        </div>
        <!-- <ModalRegister :listFakultas="listFakultas" @refresh="getData()" /> -->
        <DataTable 
          class="p-datatable"
          tableStyle="min-width: 50rem" 
          :value="items" 
          paginator 
          :rows="$store.state.pagination.row" 
          :rowsPerPageOptions="$store.state.pagination.perPage"
          v-model:filters="filters"
        >
          <template #header>
            <div class="flex justify-content-end">
              <span class="p-input-icon-left">
                <i class="pi pi-search" />
                <InputText v-model="filters['global'].value" placeholder="Search" />
              </span>
            </div>
          </template>
          <Column field="no" header="No" class="text-center" style="width: 0px">
            <template #body="{ index }">
              {{1 + index}}
            </template>
          </Column>
          <Column field="nama_fakultas" header="Nama Fakultas" class="" style=""></Column>
          <Column field="nama_prodi" header="Nama Prodi" class="" style=""></Column>
          <!-- <Column field="actions" header="" bodyClass="text-center" style="width: 0px">
            <template #body="{ data }">
              <div class="flex sm:flex-row">
                <div class="m-1">
                  <ModalUpdate :data="data" :listFakultas="listFakultas" @refresh="getData()" />
                </div>
                <div class="m-1">
                  <ModalDelete :data="data" @refresh="getData()" />
                </div>
              </div>
            </template>
          </Column> -->
        </DataTable>
      </div>
    </div>
  </div>
</template>

<script>
import { FilterMatchMode } from 'primevue/api';
// import ModalRegister from './modalRegister.vue'
// import ModalUpdate from './modalUpdate.vue'
// import ModalDelete from './modalDelete.vue'
export default {
  components: {
    // ModalRegister,
    // ModalUpdate,
    // ModalDelete,
  },
  data() {
    return {
      items: [],
      listFakultas: [],
      filters: {
        global: { value: null, matchMode: FilterMatchMode.CONTAINS },
      },
    };
  },
  async mounted() {
    const vm = this
    vm.getData()
    vm.getDataOther()
  },
  methods: {
    async getData(){
      const vm = this
      const res = await vm.$axios.post('prodi/list')
      console.log(res)
      if(res.data.status == 200){
        vm.items = res.data.data
      }
    },
    async getDataOther(){
      const vm = this
      const fakultas = await vm.$axios.post('fakultas/list')
      // console.log(res)
      if(fakultas.data.status == 200){
        vm.listFakultas = fakultas.data.data
      }
    },
  },
};
</script>